.logo-appear-active {
  -webkit-animation: bounce-in-top 2000ms both;
  animation: bounce-in-top 2000ms both;
}
.items-enter {
  transform: scale(0.1) translateX(-100%);
}
.items-enter-active {
  transform: scale(1) translateX(0);
  transition: all 300ms linear;
}
.items-exit {
  transform: scale(1) translateX(0%);
}
.items-exit-active {
  transform: scale(0.1) translateX(100%);
  transition: all 300ms linear;
}

